<template>
    <div class="container">
        <h5-input class="h5-input-psw" type="password" focus v-model="password" placeholder="请输入新密码"/>
        <h5-input class="h5-input-psw2nd" type="password" focus v-model="password2nd" placeholder="请再次输入密码"/>
        <button type="button" :disabled="password===''||password2nd===''" class="H5-btn H5-btn-primary next-step" @click="step3">
            确认
        </button>
        <remote-js js-url="//res.wx.qq.com/open/js/jweixin-1.4.0.js"/>
        <remote-js js-url="https://js.cdn.aliyun.dcloud.net.cn/dev/uni-app/uni.webview.1.5.1.js"/>
    </div>
</template>

<script>
    import h5Input from '@/components/form/h5-input.vue'
    import remoteJs from "@/components/remote-js"

    export default {
        name: "alter-psw",
        components: {
            h5Input,
            remoteJs,
        },
        data() {
            return {
                password: "",
                password2nd: "",
                connected2uni: "",
            }
        },
        methods: {
            step3() {
                let that = this;
                if (this.password !== this.password2nd) {
                    return
                }
                if (that.connected2uni) {
                    // eslint-disable-next-line no-undef
                    uni.postMessage({
                        data: {
                            "newpsw": that.password
                        }
                    });
                } else {
                    console.log("完成修改密码，前往登陆")
                }
            }
        },
        create() {
            let that = this;
            setTimeout(() => {
                if (window.UniAppJSBridge) {
                    that.connected2uni = true;
                } else {
                    document.addEventListener('UniAppJSBridgeReady', function () {
                        that.connected2uni = true;
                    });
                }
            }, 500);
        }
    }
</script>

<style lang="scss" scoped>
    .h5-input-psw {
        width: 100%;
    }

    .h5-input-psw2nd {
        margin-top: $spacing-col-sm;
        width: 100%;
    }

    .next-step {
        margin-top: $spacing-col-lg;
        margin-left: $spacing-row-base;
        margin-right: $spacing-row-base;
    }
</style>